.rowArea {
    height: 100%;
}

.center {
    text-align: center;
}

.btn-toolbar {
    justify-content: space-evenly;
}

.jumbotron {
    text-align: center;
    border-radius: 0;
}

.navbar-nav {
    justify-content: space-evenly;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5%;
    border: 1px solid gray;
    border-radius: 5px;
    flex-wrap: wrap;
}

.projectItem {
    text-align: center;
    border: 1px solid gray;
    margin: 1%;
    min-height: 150px;
}

#projectArea {
    margin: 1%;
}

.container {
    margin-bottom: 10%;
    margin-top: 10%;
}

.nav-link {
    color: rgba(0, 0, 0, .5);
    font-weight: bold;
    cursor: pointer;
}

.navbar-light:hover .navbar-nav:hover .nav-link:hover {
    color: black;
}

.hide {
    display: none;
}

.card-footer, .card-header {
    text-align: center;
}

.card {
    font-size: 0.8em;
}

.card-link + .card-link {
    margin-left: 0;
}

.btn-link {
    color: gray;
}

.btn-link:hover {
    color: black;
}

canvas {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
  width: 50%;
}

.projectButton {
    border: 0;
    border-radius: 0;
    padding: 0;
    background: none !important;
    color: #007bff;
    font-size: 1em;
    cursor: pointer;
}

.projectButton:hover {
    color: #3366BB;
    border: none;
    outline: none;
}

.pagination {
    justify-content: center;
}

@media screen and (max-width: 500px) {
    .viewPDF {
        display: none;
    }
}

@media (min-width: 576px) {
    .card-deck .card {
        flex: 4 0 auto;
        margin-bottom: 15px
    }
}